.ruleGroup {
  padding: 0.5rem;
  border: 1px solid rgb(246, 51, 102);
  border-radius: 4px;
  background: rgba(246, 51, 102, 0.1);
}
.ruleGroup .rule,
.ruleGroup .ruleGroup {
  margin-top: 0.5rem;
  margin-left: 0.5rem;
}
.ruleGroup .ruleGroup-combinators.betweenRules {
  margin-top: 0.5rem;
}
.ruleGroup .ruleGroup-notToggle {
  margin-right: 0.5rem;
}
.ruleGroup-addGroup, 
.ruleGroup-addRule {
  border: 0;
  margin-left: 0.5rem;
  font-size: small;
  box-shadow: none;
  border-radius: 2px;
  background: rgba(246, 51, 102, 0.3);
}

.ruleGroup-combinators,
.rule-fields, .rule-operators {
  border: 0;
  margin-left: 0.5rem;
  font-size: small;
  box-shadow: none;
  border-radius: 2px;
  background: none;
}
.rule-value {
  border: 0;
  box-shadow: none;
  font-size: small;
  background: white;
}
.rule-remove, .ruleGroup-remove {
  border: 1px solid rgb(246, 51, 102);
  margin-left: 0.4rem;
  font-size: smaller;
  font-weight: bold;
  font-family: monospace;
  border-radius: 2px; 
  background: rgba(246, 51, 102, 0.3); 
}